.headerBackground {
  width: 100vw;
  height: 100vh;
  background-color: white;
  /* background-color: #282623; */
  background-image: linear-gradient(to bottom, #282623);
  display: flex;
  /*  justify-content: center; */
  align-items: center;
  flex-direction: column;
  font-family: 'Montserrat';

  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  .underConstruction {
    background-color: rgba(133, 60, 40, 0.566);
    font-weight: 900;
    font-size: 5rem;
    z-index: 4;
    rotate: 15deg;
    margin-top: 15rem;
    position: absolute;
  }
  .flexAbsolute {
    display: flex;
    /* align-items: center; */
    /* justify-content: center; */
    flex-direction: row;
    /* text-align: start; */
    /*  z-index: 1; */
    /* background-color: rgba(209, 70, 60, 0.479); */
    margin-top: 3rem;
    /*  margin-left: 6rem; */
    width: 100%;

    .flexName {
      flex-wrap: wrap;
      z-index: 1;
      margin: 0;
      position: absolute;
      /*    top: 8px;
      left: 16px; */

      h4 {
        font-weight: bolder;
        color: black;
        /* color: #FC0A9D; */
        font-size: 3.5rem;
        font-family: 'Montserrat';
        font-size: 1rem;
        margin: 0;
        /*  color: rgb(49, 53, 71); */
      }

      h1 {
        /*  color: rgb(49, 53, 71); */
        font-family: 'Cedarville Cursive', cursive;
        font-size: 3rem;
        margin: -2rem;
        font-weight: bolder;

      }

      /*  span {
      z-index: 1;
      display: inline-block;
      font-weight: bolder;
      color: black;
      /* color: #FC0A9D; */
      /* font-size: 3.5rem; */
      /* margin: 0;
     } */

    }

    .flexSelfie {
      #selfieNew {
        margin-top: 0;
        width: 50rem;
      }
    }



    .arrow {
      margin-top: 8rem;
      color: white;
      font-size: 2rem;
    }

    .bounce {
      -moz-animation: bounce 2s infinite;
      -webkit-animation: bounce 2s infinite;
      animation: bounce 2s infinite;
    }

    @keyframes bounce {

      0%,
      20%,
      50%,
      80%,
      100% {
        transform: translateY(0);
      }

      40% {
        transform: translateY(-30px);
      }

      60% {
        transform: translateY(-15px);
      }
    }

    .container {
      display: inline-block;
    }

  }


  @media screen and (max-width: 480px) {
    .header {


      h4 {
        margin-top: 7rem;
        font-size: 1.3rem;
        margin-bottom: 0;

      }

      h1 {
        margin: 0;
        font-size: 1.7rem;
        margin-bottom: 0;

        span {
          font-size: 2rem;
          margin: 0;
        }
      }

      .andImDiv {
        margin-top: -1rem;
        width: 100%;

        h3 {
          text-align: center;
          color: rgb(49, 53, 71);
          font-family: 'Gaegu', cursive;
          font-size: 1rem;
        }

        h2 {
          font-size: 1rem;
          padding: 4px 12px;



        }

        @keyframes typewriter {
          from {
            width: 0%;
          }

          to {
            width: 102%;
          }


        }

      }


      .futureCollege {
        display: inline-block;
        font-size: 2rem;
        margin-top: 1rem;
      }

      .arrow {
        margin-top: 7rem;
        font-size: 2rem;
      }

    }
  }


  #navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40rem;
  }



